body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.mainContainer {
    height: 100vh;
    background-image: url(/mosam/static/media/background.f251f4b1.jpg);
    background-size: cover;
}

.secoundaryContainer {
    height: 91.5%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.card {
    width: 60%;
    height: 100%;
    border-radius: 0 !important;
    padding: 30px 40px;
    padding-bottom: 0;
    background: #E8E8E8 !important;
}

::-webkit-scrollbar {
    width: 0px;
}

.imageStyle {
    width: 30%;
}

.errorDiv {
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    color: grey;
    margin-top: 100px;
}

.center {
    text-align: center;
}

.rowStyle {
    width: 100%;
    padding: 10px 0;
}

.rowStyle:hover {
    background: lightgrey;
}

.padding_5 {
    padding-top: 5px;
}

.sectionTop {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    margin-bottom: 40px;
}

.countryInput {
    -webkit-flex-grow: 5;
            flex-grow: 5;
    margin-right: 50px;
}

.cityInput {
    -webkit-flex-grow: 5;
            flex-grow: 5;
}

.clickButton {
    margin-top: 30px;
    margin-left: 50px;
}
.sectionBottom {
    width: 100%;
    height: 70%;
}

.headerStyle {
    width: 100%;
    padding-bottom: 20px;
    font-size: 18px;
    font-weight: bold;
}

.rowContainer {
    height: 100%;
    width: 100%;
    overflow: auto;
}
.iconStyle {
    font-size: 30px !important;
}
